	module.exports = {"data":{"markdownRemark":{"html":"<pre><code class=\"language-HTML\">&#x3C;!DOCTYPE html PUBLIC \"-//W3C//DTD HTML 3.2 Final//EN\">\n&#x3C;html>\n  &#x3C;body>\n    &#x3C;p>もじもじ文字&#x3C;/p>\n  &#x3C;/body>\n&#x3C;/html>\n</code></pre>\n<h2>解説</h2>\n<p>まずは <code>!DOCTYPE</code>。\n普通 <code>&#x3C;～></code> で囲まれているものを「タグ」と呼ぶが、<code>!DOCTYPE</code> のみ「宣言」と呼ぶ。\nこれは後に続く <code>&#x3C;html></code>～<code>&#x3C;/html></code> のバージョンを宣言する重要なものだ。\nとりあえず HTML のバージョンは比較的使いやすい 3.2 としておく。\nよく解説書でこの宣言が冒頭にないことがよくあるが、これは大きな間違い。 </p>\n<p>次に <code>&#x3C;html></code>。\nこれは <code>&#x3C;html></code>～<code>&#x3C;/html></code> で囲まれた部分が HTML の中身だと思ってほしい。</p>\n<p><code>&#x3C;body></code>。これで囲まれた部分は文書や画像など、目に見える部分とみなされる。\nここまで見ればわかるだろうが基本的にタグは <code>&#x3C;～></code> で始まり <code>&#x3C;/～></code> で終わる。</p>\n<p><code>&#x3C;p></code> はこれで囲まれた部分が段落であることを記す。\nこの中に思いのまま文書を書けばとりあえずホームページとしては出来上がる。</p>","frontmatter":{"cover":null,"date":"2001-09-21T15:19:00+09:00","strDate":"2001/9/21","title":"HTML講座1"}}},"pathContext":{"path":"/ja/blog/2001/09/21-html%e8%ac%9b%e5%ba%a71/","langKey":"ja"}}

